.certificates {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3vw 0vw;
    background: linear-gradient(180deg, rgb(120, 150, 200) 0%, rgb(240, 240, 240) 100%);
}

.certificates h2 {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 2rem;
}

.certificates .links {
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 48px;
}

.certificates .links a {
    color: #000000;
    font-size: 22px;
    position: relative;
    z-index: 1;
}

@media (min-width: 576px) {
    .certificates .links a {
        font-size: 26px;
    }
}

.certificates .links a:hover {
    color: #FFFFFF;
    font-weight: 500;
}

.certificates .links a::before {
    content: "";
    position: absolute;
    height: 50%;
    width: 0;
    background: rgb(0, 0, 0);
    z-index: -1;
    right: 0;
    bottom: 50%;
    
    transition: all 0.3s;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-top: solid 2px white;
}

.certificates .links a::after {
    content: "";
    position: absolute;
    height: 50%;
    width: 0;
    background: linear-gradient(180deg, rgb(0, 0, 0) 0%, rgb(240, 240, 240) 120%);
    z-index: -1;
    left: 0;
    top: 50%;
    
    transition: all 0.3s;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border-bottom: solid 2px white;
}

.certificates .links a:hover::before {
    width: calc(100% + 10px);
    right: -5px;
}

.certificates .links a:hover::after {
    width: calc(100% + 10px);
    left: -5px;
}

@media (min-width: 576px) {
    .certificates .links a:hover::before {
        width: calc(100% + 60px);
        right: -30px;
    }
    
    .certificates .links a:hover::after {
        width: calc(100% + 60px);
        left: -30px;
    }
}